.predictionSection > form {
	display: flex;
	margin: 20px;
	width: 600px;
	justify-content: space-around;
}
.predictionSection__right > p,
.predictionSection__left > p {
	display: flex;
	padding: 5px;
	justify-content: space-between;
}
.predictionSection {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}
input {
	width: 50px;
	margin-left: 10px;
}
.predictionSection__predictBTN {
	width: 100px;
	font-size: 15px;
	padding: 5px;
	margin: 20px 0;
	float: right;
	background-color: rgb(158, 0, 158);
	outline: none;
	border-radius: 5px;
	border: 2px rgb(202, 0, 202) solid;
	cursor: pointer;
	color: white;
	font-weight: 600;
}
.predictionSection__range {
	border: 1px rgb(255, 161, 255) solid;
	display: flex;
	width: 500px;
	padding: 15px;
	flex-direction: column;
	justify-content: center;
}
.predictionSection__range > p > span {
	font-weight: 700;
	color: red;
}
.predictionSection__range > p {
	font-weight: 500;
	padding: 5px 0;
	font-size: 17px;
}
.predictionSection__rule {
	margin: 10px;
	font-weight: 600;
	font-size: 15px;
}
.predictionSection__rule > p {
	padding: 2px;
}

.predictionSection__resultPositive {
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: rgb(255, 195, 195);
	text-transform: uppercase;
	color: rgb(39, 39, 39);
	padding: 30px 100px;
	margin: 40px;
	font-size: 20px;
}

.predictionSection__resultNegative {
	display: flex;
	align-items: center;
	color: rgb(39, 39, 39);
	justify-content: center;
	background-color: rgb(200, 255, 195);
	text-transform: uppercase;
	margin: 40px;
	padding: 30px 100px;
	font-size: 20px;
}
